<style>
.content {
  height: calc(100vh - 60px);
  background-color: red;
  width: 100%;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.footer {
  position: absolute;
  height: 60px;
  width: 100%;
  bottom: 0;
  background-color: #909090;
  border-top: 1px #909990 solid;
  display: flex;
  justify-content: space-between;
}
.option {
    color: #ffffff;
    display: flex;
    width: 50%;
    justify-content: center;
    align-items: center;
    font-size: 2em;
  }
.subtraction {
  border-right: 1px solid #fff;
}
</style>
<template>
  <div>
    <div class="content">
      conut: {{counter}}
    </div>
    <div class="footer">
      <span class="option subtraction" @click='decrement'>-</span>
      <span class="option add" @click='increment'>+</span>
    </div>
  </div>
</template>

<script>
// Use Vuex
import store from "./store";

export default {
  computed: {
    counter() {
      return store.state.count
    }
  },
  created(){

},

  methods: {
    increment() {
      store.commit("increment")
    },
    decrement() {
      store.commit("decrement")
    }
  }
};
</script>




